.rf-callout[class^="rf-fi-"]::before, .rf-callout[class*=" rf-fi-"]::before {
  font-family: icons !important;
  font-style: normal;
  font-weight: normal !important;
  font-variant: normal;
  text-transform: none;
  line-height: 1; }

.rf-callout {
  position: relative;
  font-family: "Marianne", arial, sans-serif;
  padding: 1.5rem;
  /*
  > .rf-icon {
    @include respond-from(md) {

    }
  }

  > .rf-fi {
    @include font-size(24);
    margin: -(space(1N)) 0 space(1N);

    @include respond-from(md) {
      position: absolute;
      top: space(2N);
      left: space(2N);
      margin: 0;
    }
  }
   */
  box-shadow: inset 0.25rem 0 0 0 var(--scheme-border); }
  .rf-callout {
    background-color: var(--g200);
    --scheme-border: var(--bf500);
    box-shadow: inset 0.25rem 0 0 0 var(--scheme-border); }
  @media (min-width: 48em) {
    .rf-callout {
      padding: 2rem 3rem; } }
  .rf-callout[class^="rf-fi-"]::before, .rf-callout[class*=" rf-fi-"]::before {
    font-size: 1.5rem;
    display: block;
    margin: -0.5rem 0 0.5rem; }
    @media (min-width: 48em) {
      .rf-callout[class^="rf-fi-"]::before, .rf-callout[class*=" rf-fi-"]::before {
        position: absolute;
        top: 1rem;
        left: 1rem;
        margin: 0; } }
  @media (min-width: 48em) {
    .rf-callout[class^="rf-fi"], .rf-callout[class*=" rf-fi"] {
      padding: 3.5rem 3rem 2.5rem; } }
