  .rf-callout__title {
    font-size: 1.375rem;
    line-height: 1.375;
    color: var(--g800);
    margin: 0 0 0.5rem; }
    :root[data-transition] .rf-callout__title {
      transition: color 0.3s; }
  .rf-callout__text {
    margin: 0;
